import "core-js/modules/es.array.push.js";
import cookies from 'vue-cookies';
import axios from '@/utils/axios';
import { mapGetters } from 'vuex';
import BaseLogin from '@/components/BaseLogin.vue';
export default {
  data() {
    return {
      isLoading: false,
      loginFields: [{
        key: 'password',
        label: '',
        placeholder: 'Please enter the authentication code',
        type: 'password',
        showPassword: true,
        icon: 'Lock'
      }]
    };
  },
  computed: {
    ...mapGetters(['userConfig']),
    ownerName() {
      return this.userConfig?.ownerName || 'OIMG';
    },
    loginTitle() {
      return `Login OIMG`;
    }
  },
  components: {
    BaseLogin
  },
  methods: {
    async handleLogin(formData) {
      const {
        password
      } = formData;
      const writtenPass = password === '' ? 'unset' : password;
      this.isLoading = true;
      const minDelayPromise = new Promise(resolve => setTimeout(resolve, 500));
      const loginPromise = axios.post('/api/login', {
        authCode: password
      }).then(res => ({
        res
      })).catch(err => ({
        err
      }));
      try {
        const [result] = await Promise.all([loginPromise, minDelayPromise]);
        if (result.res && result.res.status === 200) {
          cookies.set('authCode', writtenPass, '14d');
          this.$router.push('/');
          this.$message.success('Login successful');
          // Keep loading true to show animation relative to redirect
        } else {
          this.isLoading = false;
          this.$message.error('Login failed. Please check your password');
        }
      } catch (err) {
        this.isLoading = false;
        this.$message.error('System error');
      }
    }
  }
};